* {
    box-sizing: border-box;
}

.player {
    position: fixed;
    bottom: 0;
    width: 100%;
    margin: 0 auto;
    background: #333;
    padding: 8px;
    height: 60px;
}

.button {
    color: white;
    width: 40px;
    margin: 3px 0 0 0;
    font-size: 16px;
    text-align: center;
    display: inline-block;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    border-color: white;
    height: 40px;
    z-index: 10;
}


.fa {
    display: inline-block;
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.fa-play{
    background-image: url("PlayButton.png");
}

.fa-pause{
    background-image: url("PauseButton.png");
}

.progressBar {
    position: relative;
    width: 92%;
    margin: 7px 0 0 16px;
    height: 13px;
    border: 1px solid #666;
    display: inline-block;
    vertical-align: top;
    top: 10px;
}

.progress {
    position: absolute;
    width: 0%;
    height: 100%;
    background: #f00;
}

.dragDot {
    position: absolute;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    margin: -4px 0 0 -9px;
    background: #f00;
    border: solid 3px #ddd;
    cursor: pointer;
    box-shadow: 0 0 5px #000, inset 0 0 4px #400;
}
