body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#allPageContent{
  padding-top: 56px;
  padding-bottom: 70px;
}

.inputButton{
  width: 100%;
  background-image: url("");
}

* {
    box-sizing: border-box;
}

.player {
    position: fixed;
    bottom: 0;
    width: 100%;
    margin: 0 auto;
    background: #333;
    padding: 8px;
    height: 60px;
}

.button {
    color: white;
    width: 40px;
    margin: 3px 0 0 0;
    font-size: 16px;
    text-align: center;
    display: inline-block;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    border-color: white;
    height: 40px;
    z-index: 10;
}


.fa {
    display: inline-block;
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.fa-play{
    background-image: url(data:image/png;base64,);
}

.fa-pause{
    background-image: url(data:image/png;base64,);
}

.progressBar {
    position: relative;
    width: 92%;
    margin: 7px 0 0 16px;
    height: 13px;
    border: 1px solid #666;
    display: inline-block;
    vertical-align: top;
    top: 10px;
}

.progress {
    position: absolute;
    width: 0%;
    height: 100%;
    background: #f00;
}

.dragDot {
    position: absolute;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    margin: -4px 0 0 -9px;
    background: #f00;
    border: solid 3px #ddd;
    cursor: pointer;
    box-shadow: 0 0 5px #000, inset 0 0 4px #400;
}

.timeline-editor__container {
  box-sizing: border-box;
  border: 1px solid #555;
  display: table;
  table-layout: fixed;
  width: 100%;
}
.timeline-editor__frames {
  display: table-cell;
  overflow-x: auto;
  vertical-align: top;
}
.timeline-editor__frames-layer {
  background-color: #ffffff;
  border-bottom: 2px solid rgba(0, 0, 0, 0.5);
  height: 1.5em;
  padding: 1px 0;
  position: relative;
  height: 68px;
}
.timeline-editor__frames-layer__item {
  box-sizing: border-box;
  line-height: 1.2em;
  color: #000;
  font-weight: bold;
  font-family: Consolas;
  cursor: move;
  display: inline-block;
  left: 0;
  min-width: 10px;
  overflow-x: hidden;
  padding: 0;
  position: absolute;
  top: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 66px;
  border-color: black;
  border: 2px;
}
.timeline-editor__frames-layer__item:after {
  content: ".";
  cursor: ew-resize;
  font-size: 0;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  width: 3px;
}
.timeline-editor__frames-ruler {
  background-color: #3aa;
  border-bottom: 1px solid black;
  display: block;
  color: #55b;
  font-size: 16px;
  height: 1.5em;
  min-width: 100%;
  background-color: #888888;
}
.timeline-editor__frames-ruler__iarrow{
  z-index: 500;
  color: #000;
  cursor: move;
  display: inline-block;
  overflow-x: hidden;
  position: relative;
  top: -24px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-color:  #888888 #888888 #888888 #9e0700;
  border-style: solid ;
  border-width: 11px;
  width: 0px;
  height: 0px;
  padding: 0;
}
.timeline-editor__frames-ruler__farrow {
  z-index: 500;
  color: #000;
  cursor: move;
  display: inline-block;
  left: 265px;
  overflow-x: hidden;
  position: relative;
  top: -24px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-left: 40px;
  margin-right: 40px;
  border-color:  #888888 #9e0700 #888888 #888888;
  border-style: solid ;
  border-width: 11px;
  width: 0px;
  height: 0px;
  padding: 0;

}
.timeline-editor__frames-ruler__second {
  border-right: 1px solid black;
  bottom: 0;
  height: 8px;

  display: inline;
  position: absolute;
  z-index: 510;
}
.timeline-editor__frames-ruler__second:nth-of-type(1) {
  left: 20px;
}
.timeline-editor__frames-ruler__second:nth-of-type(2) {
  left: 40px;
}
.timeline-editor__frames-ruler__second:nth-of-type(3) {
  left: 60px;
}
.timeline-editor__frames-ruler__second:nth-of-type(4) {
  left: 80px;
}
.timeline-editor__frames-ruler__second:nth-of-type(5) {
  height: 16px;
  right: 0px;
}
.timeline-editor__frames-ruler-s10s {
  display: inline-block;
  font-size: 14px;
  height: 100%;
  padding: 0;
  position: relative;
  text-align: center;
  width: 100px;
}
.timeline-editor__frames-ruler-s10s span {
  font-style: italic;
  font-weight: bold;
  padding-bottom: 2px;
  color: white;
}
.timeline-editor__layers {
  background-color: #1d1d1d;
  border-right: 2px solid #1d1d1d;
  color: #ffffff;
  font-family: Consolas;
  font-weight: bold;
  display: table-cell;
  vertical-align: top;
  width: 77px;
}
.timeline-editor__layers-header {
  border-bottom: 1px solid black;
  font-weight: 600;
  line-height: 1em;
  padding: 0.25em 0;
  text-align: center;
}
.timeline-editor__layers ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
