.timeline-editor__container {
  box-sizing: border-box;
  border: 1px solid #555;
  display: table;
  table-layout: fixed;
  width: 100%;
}
.timeline-editor__frames {
  display: table-cell;
  overflow-x: auto;
  vertical-align: top;
}
.timeline-editor__frames-layer {
  background-color: #ffffff;
  border-bottom: 2px solid rgba(0, 0, 0, 0.5);
  height: 1.5em;
  padding: 1px 0;
  position: relative;
  height: 68px;
}
.timeline-editor__frames-layer__item {
  box-sizing: border-box;
  line-height: 1.2em;
  color: #000;
  font-weight: bold;
  font-family: Consolas;
  cursor: move;
  display: inline-block;
  left: 0;
  min-width: 10px;
  overflow-x: hidden;
  padding: 0;
  position: absolute;
  top: 0;
  user-select: none;
  height: 66px;
  border-color: black;
  border: 2px;
}
.timeline-editor__frames-layer__item:after {
  content: ".";
  cursor: ew-resize;
  font-size: 0;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  width: 3px;
}
.timeline-editor__frames-ruler {
  background-color: #3aa;
  border-bottom: 1px solid black;
  display: block;
  color: #55b;
  font-size: 16px;
  height: 1.5em;
  min-width: 100%;
  background-color: #888888;
}
.timeline-editor__frames-ruler__iarrow{
  z-index: 500;
  color: #000;
  cursor: move;
  display: inline-block;
  overflow-x: hidden;
  position: relative;
  top: -24px;
  user-select: none;
  border-color:  #888888 #888888 #888888 #9e0700;
  border-style: solid ;
  border-width: 11px;
  width: 0px;
  height: 0px;
  padding: 0;
}
.timeline-editor__frames-ruler__farrow {
  z-index: 500;
  color: #000;
  cursor: move;
  display: inline-block;
  left: 265px;
  overflow-x: hidden;
  position: relative;
  top: -24px;
  user-select: none;
  margin-left: 40px;
  margin-right: 40px;
  border-color:  #888888 #9e0700 #888888 #888888;
  border-style: solid ;
  border-width: 11px;
  width: 0px;
  height: 0px;
  padding: 0;

}
.timeline-editor__frames-ruler__second {
  border-right: 1px solid black;
  bottom: 0;
  height: 8px;

  display: inline;
  position: absolute;
  z-index: 510;
}
.timeline-editor__frames-ruler__second:nth-of-type(1) {
  left: 20px;
}
.timeline-editor__frames-ruler__second:nth-of-type(2) {
  left: 40px;
}
.timeline-editor__frames-ruler__second:nth-of-type(3) {
  left: 60px;
}
.timeline-editor__frames-ruler__second:nth-of-type(4) {
  left: 80px;
}
.timeline-editor__frames-ruler__second:nth-of-type(5) {
  height: 16px;
  right: 0px;
}
.timeline-editor__frames-ruler-s10s {
  display: inline-block;
  font-size: 14px;
  height: 100%;
  padding: 0;
  position: relative;
  text-align: center;
  width: 100px;
}
.timeline-editor__frames-ruler-s10s span {
  font-style: italic;
  font-weight: bold;
  padding-bottom: 2px;
  color: white;
}
.timeline-editor__layers {
  background-color: #1d1d1d;
  border-right: 2px solid #1d1d1d;
  color: #ffffff;
  font-family: Consolas;
  font-weight: bold;
  display: table-cell;
  vertical-align: top;
  width: 77px;
}
.timeline-editor__layers-header {
  border-bottom: 1px solid black;
  font-weight: 600;
  line-height: 1em;
  padding: 0.25em 0;
  text-align: center;
}
.timeline-editor__layers ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
