.button-play {
  z-index: 1050;

  width: 74px;
  height: 67px;
  box-sizing: border-box;
  background-image: url("framePlay.png");
  border: none;
}
.button-pause {
  z-index: 1050;
  width: 74px;
  height: 67px;
  background-image: url("framePause.png");
  border: none;
}

.audioPlayer {
  width: 100%;
  position: fixed;
  bottom: 0;
}